// src/contexts/LearningContext.js

import React, {
  createContext,
  useContext,
  useState,
  useEffect,
  useCallback,
  useRef,
} from 'react';
import {
  collection,
  doc,
  getDocs,
  getDoc,
  writeBatch,
  arrayUnion,
  setDoc,
  updateDoc,
  query,
  where,
  orderBy,
} from 'firebase/firestore';
import { useAuth } from './AuthContext';
import { db } from '../firebase';

const LearningContext = createContext();
export const useLearning = () => useContext(LearningContext);

export const LearningProvider = ({ children }) => {
  const { user, loading: authLoading } = useAuth();

  // State for course data (selected course, next lesson/workbook)
  const [selectedCourse, setSelectedCourse] = useState(null);
  const [nextLesson, setNextLesson] = useState(null);
  const [nextWorkbook, setNextWorkbook] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // New state for courses list fetched via fetchCourses function
  const [courses, setCourses] = useState([]);
  const [loadingCourses, setLoadingCourses] = useState(false);
  const [coursesError, setCoursesError] = useState(null);
  const coursesFetched = useRef(false);

  // State for user interactions (e.g., quiz answers) and current content ID
  const [userInteractions, setUserInteractions] = useState({});
  const [currentLessonOrWorkbookID, setCurrentLessonOrWorkbookID] = useState(null);

  // -----------------------------
  // 1. SAVE TEST RESULT FUNCTION
  // -----------------------------
  const saveTestResult = async (lessonID, testResult) => {
    try {
      if (!lessonID || typeof lessonID !== 'string') {
        throw new Error('Invalid lessonID provided. It must be a string.');
      }
      if (!testResult) throw new Error('Invalid test result provided.');

      // Split lessonID into moduleID and lessonID
      const [moduleID, contentID] = lessonID.split('/');
      if (!moduleID || !contentID) {
        throw new Error('Invalid lessonID format. Expected "moduleID/lessonID".');
      }

      const interactionRef = doc(
        db,
        'courses',
        user.courseID,
        'modules',
        moduleID,
        'lessons',
        contentID,
        'interactions',
        user.uid
      );

      const batch = writeBatch(db);
      batch.set(interactionRef, testResult, { merge: true });
      await batch.commit();
      // Optionally refresh user state if needed (e.g., via useAuth)
      fetchUserAndCourseData();
    } catch (err) {
      console.error('Error saving test result:', err.message || err);
      throw err;
    }
  };

  // -----------------------------
  // 2. FETCH TEST RESULTS FUNCTION
  // -----------------------------
  const fetchTestResults = async (moduleID, lessonID) => {
    try {
      if (!user) throw new Error('User is not logged in.');
      if (!moduleID || !lessonID)
        throw new Error('Invalid moduleID or lessonID provided.');

      const interactionRef = doc(
        db,
        'courses',
        user.courseID,
        'modules',
        moduleID,
        'lessons',
        lessonID,
        'interactions',
        user.uid
      );

      console.log('Fetching test results from:', interactionRef.path);
      const interactionDoc = await getDoc(interactionRef);
      if (interactionDoc.exists()) {
        return {
          lessonID,
          userInteractions: interactionDoc.data(),
        };
      } else {
        console.warn(`No interactions found for lesson ${lessonID}`);
        return {
          lessonID,
          userInteractions: {},
        };
      }
    } catch (err) {
      console.error('Error fetching test results:', err.message || err);
      return {
        lessonID,
        userInteractions: {},
        error: 'Failed to fetch test results.',
      };
    }
  };

  // -----------------------------
  // 3. FETCH USER & COURSE DATA FUNCTION
  // -----------------------------
  const fetchUserAndCourseData = useCallback(async () => {
    if (!user) return;

    setLoading(true);
    setError(null);

    try {
      // 3a. Fetch user document
      const userDocRef = doc(db, 'users', user.uid);
      const userDoc = await getDoc(userDocRef);
      const userData = userDoc.exists() ? userDoc.data() : {};

      const finishedLessons = userData.finishedLessons || [];
      const finishedWorkbooks = userData.finishedWorkbooks || [];

      // 3b. Fetch course document using user.courseID
      if (user.courseID) {
        const courseDocRef = doc(db, 'courses', user.courseID);
        const courseDoc = await getDoc(courseDocRef);

        if (courseDoc.exists()) {
          const courseData = { id: courseDoc.id, ...courseDoc.data() };

          // 3c. Fetch modules
          const modulesSnapshot = await getDocs(collection(courseDocRef, 'modules'));
          const modulesData = await Promise.all(
            modulesSnapshot.docs.map(async (moduleDoc) => {
              const moduleData = { id: moduleDoc.id, ...moduleDoc.data() };

              // 3d. Fetch lessons in each module
              const lessonsSnapshot = await getDocs(
                collection(courseDocRef, 'modules', moduleDoc.id, 'lessons')
              );
              const lessonsData = await Promise.all(
                lessonsSnapshot.docs.map(async (lessonDoc) => {
                  const lessonData = {
                    id: lessonDoc.id,
                    moduleID: moduleDoc.id,
                    ...lessonDoc.data(),
                  };

                  // Fetch user interactions for the lesson
                  const interactionRef = doc(
                    db,
                    'courses',
                    user.courseID,
                    'modules',
                    moduleDoc.id,
                    'lessons',
                    lessonDoc.id,
                    'interactions',
                    user.uid
                  );
                  const interactionDoc = await getDoc(interactionRef);
                  lessonData.userInteractions = interactionDoc.exists()
                    ? interactionDoc.data()
                    : {};
                  return lessonData;
                })
              );

              // 3e. Fetch workbooks in each module
              const workbooksSnapshot = await getDocs(
                collection(courseDocRef, 'modules', moduleDoc.id, 'workbooks')
              );
              const workbooksData = await Promise.all(
                workbooksSnapshot.docs.map(async (workbookDoc) => {
                  const workbookData = {
                    id: workbookDoc.id,
                    moduleID: moduleDoc.id,
                    ...workbookDoc.data(),
                  };

                  // Fetch user interactions for the workbook
                  const interactionRef = doc(
                    db,
                    'courses',
                    user.courseID,
                    'modules',
                    moduleDoc.id,
                    'workbooks',
                    workbookDoc.id,
                    'interactions',
                    user.uid
                  );
                  const interactionDoc = await getDoc(interactionRef);
                  workbookData.userInteractions = interactionDoc.exists()
                    ? interactionDoc.data()
                    : {};
                  return workbookData;
                })
              );

              return {
                ...moduleData,
                lessons: lessonsData,
                workbooks: workbooksData,
              };
            })
          );

          const fullCourseData = {
            ...courseData,
            modules: modulesData,
          };

          setSelectedCourse(fullCourseData);

          // Flatten lessons and workbooks
          const allLessons = modulesData.flatMap((m) => m.lessons || []);
          const allWorkbooks = modulesData.flatMap((m) => m.workbooks || []);

          // Determine next available lesson and workbook
          const unfinishedLessons = allLessons.filter(
            (lesson) => !finishedLessons.some((f) => f.lessonID === lesson.id)
          );
          const unfinishedWorkbooks = allWorkbooks.filter(
            (workbook) => !finishedWorkbooks.some((f) => f.workbookID === workbook.id)
          );

          setNextLesson(unfinishedLessons[0] || null);
          setNextWorkbook(unfinishedWorkbooks[0] || null);
        } else {
          setError('Selected course does not exist.');
        }
      }
    } catch (err) {
      console.error('Error fetching user or course data:', err);
      setError('Failed to fetch course data.');
    } finally {
      setLoading(false);
    }
  }, [user]);

  // -----------------------------
  // 4. SAVE INTERACTIONS IMMEDIATELY
  // -----------------------------
  const saveInteractionsImmediately = async () => {
    if (!user || !currentLessonOrWorkbookID) return;
    try {
      const [type, moduleID, contentID] = currentLessonOrWorkbookID.split('/');
      const interactionRef = doc(
        db,
        'courses',
        user.courseID,
        'modules',
        moduleID,
        type === 'lesson' ? 'lessons' : 'workbooks',
        contentID,
        'interactions',
        user.uid
      );
      await setDoc(interactionRef, userInteractions, { merge: true });
    } catch (err) {
      console.error('Error saving user interactions:', err);
      setError('Failed to save interactions.');
    }
  };

  // Debounce saving interactions to limit writes
  useEffect(() => {
    const handler = setTimeout(() => {
      saveInteractionsImmediately();
    }, 500);
    return () => clearTimeout(handler);
  }, [userInteractions, user, currentLessonOrWorkbookID]);

  // Save interactions on browser/tab close
  useEffect(() => {
    const handleBeforeUnload = () => {
      saveInteractionsImmediately();
    };
    window.addEventListener('beforeunload', handleBeforeUnload);
    return () => window.removeEventListener('beforeunload', handleBeforeUnload);
  }, [userInteractions, user, currentLessonOrWorkbookID]);

  const updateUserInteraction = (key, interaction) => {
    setUserInteractions((prev) => ({
      ...prev,
      [key]: interaction,
    }));
  };

  // -----------------------------
  // 5. FINISH A LESSON
  // -----------------------------
  const finishLesson = async (lessonID, lessonStats = {}) => {
    try {
      if (!user || !lessonID) return;
      const userRef = doc(db, 'users', user.uid);
      const [moduleID, contentID] = lessonID.split('/');

      // Prevent duplicate finish
      if (user.finishedLessons && user.finishedLessons.some(l => l.lessonID === contentID)) {
        console.log("Lesson already finished.");
        return;
      }

      const interactionRef = doc(
        db,
        'courses',
        user.courseID,
        'modules',
        moduleID,
        'lessons',
        contentID,
        'interactions',
        user.uid
      );

      const batch = writeBatch(db);
      const newFinishedLesson = {
        lessonID: contentID,
        ...lessonStats,
        date: new Date().toISOString(),
      };

      batch.update(userRef, {
        finishedLessons: arrayUnion(newFinishedLesson),
        currentLessonID: lessonID,
      });

      batch.set(interactionRef, userInteractions, { merge: true });
      await batch.commit();
      fetchUserAndCourseData();
    } catch (err) {
      console.error('Error finishing lesson:', err);
      setError('Failed to finish the lesson.');
    }
  };

  // -----------------------------
  // 6. FINISH A WORKBOOK
  // -----------------------------
  const finishWorkbook = async (workbookID, workbookStats = {}) => {
    try {
      if (!user || !workbookID) return;
      const userRef = doc(db, 'users', user.uid);
      const [moduleID, contentID] = workbookID.split('/');

      // Prevent duplicate finish
      if (user.finishedWorkbooks && user.finishedWorkbooks.some(w => w.workbookID === contentID)) {
        console.log("Workbook already finished.");
        return;
      }

      const interactionRef = doc(
        db,
        'courses',
        user.courseID,
        'modules',
        moduleID,
        'workbooks',
        contentID,
        'interactions',
        user.uid
      );

      const batch = writeBatch(db);
      const newFinishedWorkbook = {
        workbookID: contentID,
        ...workbookStats,
        date: new Date().toISOString(),
      };

      batch.update(userRef, {
        finishedWorkbooks: arrayUnion(newFinishedWorkbook),
        currentWorkbookID: workbookID,
      });

      batch.set(interactionRef, userInteractions, { merge: true });
      await batch.commit();
      fetchUserAndCourseData();
    } catch (err) {
      console.error('Error finishing workbook:', err);
      setError('Failed to finish the workbook.');
    }
  };

  // -----------------------------
  // 7. NEW: FETCH COURSES FUNCTION
  // -----------------------------
  const fetchCourses = useCallback(async () => {
    if (coursesFetched.current) return; // Avoid duplicate calls
    setLoadingCourses(true);
    setCoursesError(null);
    try {
      const coursesSnapshot = await getDocs(collection(db, 'courses'));
      const fetchedCourses = coursesSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data(),
      }));
      setCourses(fetchedCourses);
      coursesFetched.current = true;
    } catch (error) {
      console.error('Error fetching courses:', error);
      setCoursesError(error);
    } finally {
      setLoadingCourses(false);
    }
  }, []);

  // -----------------------------
  // 8. FETCH USER & COURSE DATA ON INIT
  // -----------------------------
  useEffect(() => {
    if (!authLoading && user) {
      fetchUserAndCourseData();
    } else {
      setLoading(false);
    }
  }, [user, authLoading, fetchUserAndCourseData]);

  // -----------------------------
  // 9. PROVIDE CONTEXT VALUES
  // -----------------------------
  const contextValue = {
    // Course and modules data
    selectedCourse,
    nextLesson,
    nextWorkbook,
    // Courses list from Firebase
    courses,
    fetchCourses,
    loadingCourses,
    coursesError,
    // Finishing functions
    finishLesson,
    finishWorkbook,
    // Loading and error states
    loading,
    error,
    // User interactions functions and state
    userInteractions,
    updateUserInteraction,
    setUserInteractions,
    setCurrentLessonOrWorkbookID,
    // Test results
    saveTestResult,
    fetchTestResults,
    // Optionally expose fetchUserAndCourseData
    fetchUserAndCourseData,
  };

  return (
    <LearningContext.Provider value={contextValue}>
      {!loading && children}
    </LearningContext.Provider>
  );
};

export default LearningContext;
